@import "https://fonts.googleapis.com/css2?family=Playwrite+IT+Moderna:wght@100..400&display=swap";
@keyframes blink {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes appear {
  0% {
    clip-path: rect(0% 100% 100% 0% round 0%);
  }

  10% {
    clip-path: rect(0.5% 99.5% 100% 0.5% round 5%);
  }

  20% {
    clip-path: rect(1% 99% 100% 1% round 5%);
  }

  30% {
    clip-path: rect(1.5% 98.5% 100% 1.5% round 10%);
  }

  40% {
    clip-path: rect(2% 98% 100% 2% round 10%);
  }

  50% {
    clip-path: rect(2.5% 97.5% 100% 2.5% round 15%);
  }

  60% {
    clip-path: rect(3% 97% 100% 3% round 15%);
  }

  70% {
    clip-path: rect(3.5% 96.5% 100% 3.5% round 15%);
  }

  80% {
    clip-path: rect(4% 96% 100% 4% round 20%);
  }

  90% {
    clip-path: rect(4.5% 95.5% 100% 4.5% round 20%);
  }

  100% {
    clip-path: rect(5% 95% 100% 5% round 20%);
  }
}

